import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { ButtonBase } from '@material-ui/core';

import { createStructuredSelector } from 'reselect';
import Utils from '../../utils';
import { saveKeyword, getHomePosts, getMorePosts } from './actions';
import { resetSelectedPost } from '../Detail/actions';

import { makeSelectNextPageToken } from './selectors';
import { limitPerPage } from '../../configs';

class PostsList extends Component {
    componentDidMount() {
        const { keyword, saveKeyword, isSearch } = this.props;
        if (!isSearch) {
            if (keyword) {
                saveKeyword(null);
            }
        }
    }

    onHandleSelectPost = post => {
        const { onHandleSelectPost, resetSelectedPost } = this.props;
        resetSelectedPost();
        onHandleSelectPost(post);
    };

    clearSearch = () => {
        const { getHomePosts, saveKeyword } = this.props;
        saveKeyword(null);
        getHomePosts({ pageToken: null, keyword: null });
    };

    viewMore = (ev, pageToken) => {
        ev.preventDefault();
        const { getMorePosts, keyword } = this.props;
        getMorePosts({ pageToken, keyword });
    };

    render() {
        const { posts, match, home, nextPageToken } = this.props;

        if (posts.length > 0) {
            return (
                <Grid className="list-wrp">
                    <Grid container spacing={32}>
                        {posts.map(post => {
                            let label = post.labels[0] + '/';
                            if (!home) {
                                label = '';
                            }
                            const postUrl = Utils.convertStringToURL(
                                post.title
                            );
                            const uri =
                                match.url +
                                label +
                                postUrl +
                                '-idpx' +
                                post.id +
                                '.html';
                            return (
                                <Grid
                                    item
                                    key={post.id}
                                    className="col-l-3 col-3 col-s-4 post-item"
                                >
                                    <div className="item-img">
                                        <Link
                                            to={uri}
                                            onClick={() =>
                                                this.onHandleSelectPost(post)
                                            }
                                        >
                                            <img
                                                src={post.images[0]}
                                                alt={post.title}
                                            />
                                        </Link>
                                    </div>
                                    <div className="item-info">
                                        <div className="title">
                                            <Link
                                                to={uri}
                                                onClick={() =>
                                                    this.onHandleSelectPost(
                                                        post
                                                    )
                                                }
                                            >
                                                <Typography
                                                    className="card-title"
                                                    gutterBottom
                                                    variant="h5"
                                                    component="h3"
                                                >
                                                    {post.title}
                                                </Typography>
                                            </Link>
                                        </div>
                                        {post.price && (
                                            <div className="price">
                                                {post.price}
                                                {post.currency && (
                                                    <span className="currency">
                                                        {post.currency}
                                                    </span>
                                                )}
                                            </div>
                                        )}
                                    </div>
                                </Grid>
                            );
                        })}
                    </Grid>
                    {posts.length >= limitPerPage &&
                        (!home && (
                            <Grid container>
                                <Grid className="paging">
                                    {nextPageToken && (
                                        <ButtonBase
                                            className="btn btn-small btn-success"
                                            onClick={ev =>
                                                this.viewMore(ev, nextPageToken)
                                            }
                                        >
                                            View More
                                        </ButtonBase>
                                    )}
                                </Grid>
                            </Grid>
                        ))}
                </Grid>
            );
        } else {
            return (
                <Grid container className="item-container">
                    Have no any post for this topid :(
                </Grid>
            );
        }
    }
}

// Define PropTypes
PostsList.propTypes = {
    posts: PropTypes.oneOfType([PropTypes.array, PropTypes.bool]).isRequired,
    keyword: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
    isSearch: PropTypes.bool,
    saveKeyword: PropTypes.func.isRequired,
    getHomePosts: PropTypes.func.isRequired,
    match: PropTypes.object.isRequired,
    home: PropTypes.bool,
    onHandleSelectPost: PropTypes.func.isRequired,
    resetSelectedPost: PropTypes.func.isRequired,
    nextPageToken: PropTypes.string,
    getMorePosts: PropTypes.func.isRequired
};

PostsList.defaultProps = {
    home: false,
    nextPageToken: null,
    isSearch: false,
    keyword: false
};

const mapStateToProps = createStructuredSelector({
    nextPageToken: makeSelectNextPageToken()
});

const mapDispatchToProps = {
    resetSelectedPost,
    saveKeyword,
    getHomePosts,
    getMorePosts
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(PostsList);
