import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import renderHTML from 'react-render-html';
import { Helmet } from 'react-helmet';
import { compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import Grid from '@material-ui/core/Grid';

import utils from '../../utils';
import { getSamePosts, selectPost } from './actions';
import { limitOtherPosts, fb_app_id } from '../../configs';
import Comments from '../Comments';
import Loading from '../../components/commons/Loading';

import ogImg from '../../assets/images/og-img.jpg';
import info from '../../assets/images/info-circle-solid.svg';

import { makeSelectPost } from './selectors';

import injectReducer from '../../utils/injectReducer';
import injectSaga from '../../utils/injectSaga';

import reducer from './reducer';
import saga from './saga';
import ImgSlider from './ImgSlider';

class Detail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            PageNoFound: false
        };
    }
    componentDidMount() {
        const { post } = this.props;

        if (!post.data) {
            this.getSelectedPost();
        }
    }

    componentDidUpdate(prevProps) {
        const { post, getSamePosts } = this.props;
        if (!post.waiting) {
            if (post.data) {
                if (post.data.id !== prevProps.post.data.id) {
                    getSamePosts({
                        label: post.data.labels[0],
                        limit: limitOtherPosts
                    });
                }
            }
        }
    }

    componentWillUnmount() {
        clearTimeout(this.timeoutID);
    }

    setPageNotFound = () => {
        this.setState({ PageNoFound: true });
    };

    getSelectedPost = () => {
        const { match } = this.props;
        const url = match.params.id;
        if (url) {
            const postId = utils.getIdFromURL(url);
            const { selectPost } = this.props;
            selectPost(postId);
        } else {
            this.setPageNotFound();
        }
    };

    handleSelectPost = selectedPost => {
        const { selectPost } = this.props;
        selectPost(selectedPost.id);
    };

    render() {
        const { post } = this.props;
        const { PageNoFound } = this.state;

        if (post.waiting) {
            return (
                <div className="loading-wrp">
                    <Loading size={30} />
                </div>
            );
        }

        if (PageNoFound) {
            return (
                <Grid container className="detail">
                    <Grid container>
                        <h2>Page not found! :(</h2>
                    </Grid>
                </Grid>
            );
        }

        if (post.data) {
            return (
                <Grid container className="detail">
                    <Helmet
                        titleTemplate={post.data.labels[0] + ' | %s'}
                        title={post.data.title}
                    />
                    <Helmet>
                        <meta name="description" content={post.data.title} />
                        <meta
                            property="og:url"
                            content={window.location.href}
                        />
                        <meta property="og:type" content="article" />
                        <meta property="og:title" content={post.data.title} />
                        <meta
                            property="og:image"
                            content={
                                post.data.images[0]
                                    ? post.data.images[0]
                                    : ogImg
                            }
                        />
                        <meta
                            property="og:description"
                            content={post.data.title}
                        />
                        <meta property="fb:app_id" content={fb_app_id} />
                        <link rel="canonical" href={window.location.href} />
                    </Helmet>
                    <Grid container className="detail-wrp">
                        <Grid item xs={12} className="detail-header">
                            <h1>{post.data.title}</h1>
                        </Grid>
                        <Grid className="detail-left">
                            <div className="top-detail">
                                <div className="slider">
                                    <ImgSlider imgs={post.data.images} />
                                </div>
                                <div className="product-info-w">
                                    {renderHTML(post.data.productInfo)}
                                    <div className="price-note">
                                        <img src={info} alt="" /> Giá tham khảo,
                                        thay đổi theo thời giá
                                    </div>
                                </div>
                            </div>
                            <Grid className="detail-content">
                                {renderHTML(post.data.content)}
                            </Grid>
                            <Grid container>
                                <Comments
                                    title={post.data.title}
                                    url={window.location.href}
                                    postId={post.data.id}
                                />
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            );
        } else {
            return (
                <Grid container className="detail">
                    <Grid container>
                        <h2>No data available! :(</h2>
                    </Grid>
                </Grid>
            );
        }
    }
}

// Define PropTypes
Detail.propTypes = {
    post: PropTypes.object.isRequired,
    selectPost: PropTypes.func.isRequired,
    getSamePosts: PropTypes.func.isRequired,
    match: PropTypes.object.isRequired
};

const mapStateToProps = createStructuredSelector({
    post: makeSelectPost()
});

const mapDispatchToProps = {
    selectPost,
    getSamePosts
};

const withConnect = connect(
    mapStateToProps,
    mapDispatchToProps
);

const withReducer = injectReducer({ key: 'detailReducer', reducer });
const withSaga = injectSaga({ key: 'detailSaga', saga });

export default compose(
    withReducer,
    withSaga,
    withConnect
)(Detail);
