import initialState from './initialState';
import * as consts from './consts';

export default function(state = initialState, action) {
    switch (action.type) {
        case consts.GET_POSTS_REQUEST:
            return state
                .setIn(['posts', 'waiting'], true)
                .setIn(['posts', 'error'], false);
        case consts.GET_POSTS_SUCCESS:
            return state
                .setIn(['posts', 'waiting'], false)
                .setIn(['posts', 'error'], false)
                .updateIn(['posts', 'data'], obj => ({
                    ...obj,
                    [action.posts.category]: action.posts.data
                }));
        case consts.GET_POSTS_ERROR:
            return state
                .setIn(['posts', 'waiting'], false)
                .setIn(['posts', 'error'], action.error)
                .setIn(['posts', 'data'], false);
        case consts.RESET_POSTS:
            return state.setIn(['posts', 'data'], false);
        default:
            return state;
    }
}
