import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import cloneDeep from 'lodash.clonedeep';

import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { createStructuredSelector } from 'reselect';

import Utils from '../../utils';
import { resetSelectedPost } from './actions';
import { limitOtherPosts } from '../../configs';
import { makeSelectSamePosts, makeSelectSelectedPost } from './selectors';

import LoadingComponent from '../../components/commons/LoadingComponent';

class SamePosts extends Component {
    componentDidMount() {}

    onHandleSelectPost = post => {
        const { onHandleSelectPost, resetSelectedPost } = this.props;
        resetSelectedPost();
        onHandleSelectPost(post);
    };

    render() {
        const { samePosts, selectedPost } = this.props;
        let samePostsFiltered = [];
        if (samePosts.waiting) {
            return (
                <Grid container spacing={32} className="other-post">
                    <LoadingComponent />
                </Grid>
            );
        }

        if (samePosts.data) {
            const samePostsCloned = cloneDeep(samePosts.data);
            samePostsFiltered = samePostsCloned.filter(
                post => post.id !== selectedPost.id
            );

            if (samePostsFiltered.length > limitOtherPosts) {
                samePostsFiltered = samePostsFiltered.pop();
            }
        }

        if (samePostsFiltered.length <= 0) {
            return (
                <Grid container spacing={32} className="other-post">
                    <Grid item className="col-l-12 col-12 col-s-12 post-item">
                        No data available
                    </Grid>
                </Grid>
            );
        }

        return (
            <div className="latest-post">
                {samePostsFiltered.map(post => {
                    const postUrl = Utils.convertStringToURL(post.title);
                    const uri =
                        '/' +
                        post.labels[0] +
                        '/' +
                        postUrl +
                        '-idpx' +
                        post.id +
                        '.html';
                    return (
                        <div
                            key={post.id}
                            className="col-l-12 col-12 col-s-6 post-item"
                        >
                            <Link
                                to={uri}
                                onClick={() => this.onHandleSelectPost(post)}
                            >
                                <div className="img">
                                    <img
                                        src={post.images[0]}
                                        alt={post.title}
                                    />
                                </div>
                                <Typography
                                    className="card-title"
                                    gutterBottom
                                    variant="h5"
                                    component="h4"
                                >
                                    {post.title}
                                </Typography>
                                {post.price && (
                                    <div className="price">
                                        {post.price}
                                        {post.currency && (
                                            <span className="currency">
                                                {post.currency}
                                            </span>
                                        )}
                                    </div>
                                )}
                            </Link>
                        </div>
                    );
                })}
            </div>
        );
    }
}

SamePosts.propTypes = {
    samePosts: PropTypes.object.isRequired,
    onHandleSelectPost: PropTypes.func.isRequired,
    resetSelectedPost: PropTypes.func.isRequired,
    selectedPost: PropTypes.oneOfType([PropTypes.bool, PropTypes.object])
        .isRequired
};

const mapStateToProps = createStructuredSelector({
    samePosts: makeSelectSamePosts(),
    selectedPost: makeSelectSelectedPost()
});

const mapDispatchToProps = {
    resetSelectedPost
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(SamePosts);
