import { put, call, all, takeEvery } from 'redux-saga/effects';
import services from '../../services/bloggerAPIs';
import * as consts from './consts';
import { setNextPageToken } from '../Home/actions';
import utils from '../../utils';

function* getPostsSaga({ payload }) {
    try {
        let posts = yield call(services.getPosts, payload);
        const postsIncludedImages = utils.getImages(posts.data.items);
        const postsIncludedPrice = utils.getPrice(postsIncludedImages);
        const postsIncludedCurrency = utils.getCurrency(postsIncludedPrice);
        const postsIncludedProductInfo = utils.getProductInfo(
            postsIncludedCurrency
        );
        let nextPageToken = null;
        if (posts.data.nextPageToken) {
            nextPageToken = posts.data.nextPageToken;
        }
        yield all([
            put({
                type: consts.GET_POSTS_SUCCESS,
                posts: {
                    data: postsIncludedProductInfo,
                    category: payload.label
                }
            }),
            put(setNextPageToken(nextPageToken))
        ]);
    } catch (error) {
        yield put({
            type: consts.GET_POSTS_ERROR,
            error: error
        });
        yield put(setNextPageToken(null));
    }
}

export default function* root() {
    yield takeEvery(consts.GET_POSTS_REQUEST, getPostsSaga);
}
